a {
    color: #fff;
    font-weight: bold;
}

.header {
    height: 15vh;
    min-height: 100px;
    max-height: 180px;
    padding: 10px;
    color: white;
    font-weight: 100;
    position: relative;
}

.projects-list {
    padding-top: 12px;
    -ms-flex-line-pack: center;
        align-content: center;
    text-align: center;
    -ms-flex-align: center;
        align-items: center;
    z-index: 2;
    /*overflow: hidden;*/
    width: 75vw;
    margin: auto;
}

@-webkit-keyframes spin {
    100% {
        -webkit-transform: rotate(90deg);
        transform: rotate(90deg);
    }
}

@keyframes spin {
    100% {
        -webkit-transform: rotate(90deg);
        transform: rotate(90deg);
    }
}

.project:hover .project-image {
    -ms-transform: scale(1.25);
        transform: scale(1.25);
    -webkit-transform: scale(1.25);
}

.project {
    position: relative;
    display: block;
    max-height: 350px;
    min-height: 150px;
    height: 18vw;
    overflow: hidden;
}

.project-title {
    font-size: 16pt;
    font-weight: 300;
    color: #fff;
    position: absolute;
    bottom: 5px;
    width: 100%;
    opacity: 0;
    z-index: 10;
}

.project:hover {
    cursor: pointer;
}

.project:hover>.project-title {
    opacity: 1;
    -o-transition: 0.5s;
    transition: 0.5s;
    -webkit-transition: 0.5s;
    /* Safari */
    cursor: pointer;
}

.project-description {
    margin-top: 0px;
}

.menus {
    position: absolute;
    right: 10px;
    top: 30px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-line-pack: center;
        align-content: center;
    vertical-align: middle;
}

.menus .btn-menu {
    color: #f0f0f0;
    text-decoration: none;
    border: none;
    background-color: transparent;
    padding: 20px;
    padding-top: 10px;
    padding-bottom: 15px;
    font-size: 10pt;
    font-weight: 100;
}

.menus .btn-menu:hover {
    color: #fff;
    /*background: linear-gradient(to bottom, #474747 0%, #212121 100%);*/
    border-bottom: 1px solid rgba(255, 255, 255, 0.25);
    padding-bottom: 10px;
    cursor: pointer;
    z-index: 0;
}

.menus .line {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    width: 80px;
    height: 1px;
    margin-top: 20px;
    background: #fff;
}

.header .title {
    position: absolute;
    left: 30px;
    top: 30px;
}

.header .title-name {
    font-family: AvenirNext;
    font-size: 28pt;
    width: 22vw;
    min-width: 120px;
    max-width: 300px;
    margin-bottom: 5px;
    color: #fff;
}

.header .title-role {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-line-pack: center;
        align-content: center;
    vertical-align: middle;
    height: 20px;
    margin-top: 5px;
    margin-bottom: 5px;
}

.header .title-role-line {
    width: 80px;
    margin-top: 9px;
    margin-right: 15px;
    height: 1px;
    background: #fff;
}

.header .title-role-text {
    color: #fff;
    font-size: 10pt;
}

.header .subtitle {
    font-weight: 100;
    font-size: 8pt;
    line-height: 8pt;
    width: 50vw;
    margin-top: 4px;
    margin-left: 0px;
    color: rgba(255, 255, 255, 0.8);
}

.header .photo {
    width: 80px;
    height: 80px;
    background: #fff;
    position: absolute;
    left: 80px;
    top: 25px;
    border-radius: 50%;
}

.header a {
    color: rgba(255, 255, 255, 0.5);
    font-weight: 100;
    text-decoration: none;
    font-size: 10pt;
}

.header a:hover {
    color: #FFF;
    font-weight: 100;
    font-size: 10pt;
    text-decoration: none;
}

.divider {
    width: 50vw;
    margin: auto;
    height: 1px;
    /* Rectangle 2: */
    /*background-image: linear-gradient(-90deg, #ffffff 0%, #ddd 50%, #ffffff 100%);*/
    opacity: 0.15;
    background-image: -webkit-gradient( linear, right top, left top, from(rgba(255, 255, 255, 0)), color-stop(50%, #ffffff), to(rgba(255, 255, 255, 0)));
    background-image: -webkit-linear-gradient( right, rgba(255, 255, 255, 0) 0%, #ffffff 50%, rgba(255, 255, 255, 0) 100%);
    background-image: -o-linear-gradient( right, rgba(255, 255, 255, 0) 0%, #ffffff 50%, rgba(255, 255, 255, 0) 100%);
    background-image: linear-gradient( -90deg, rgba(255, 255, 255, 0) 0%, #ffffff 50%, rgba(255, 255, 255, 0) 100%);
    /* Sketch doesnt export gradient borders at this point */
}

.canvas-3d {
    position: absolute;
    top: 0px;
    left: 0px;
}

.contact {
    position: fixed;
    left: Calc( 50% - 50px);
    bottom: 0px;
    font-size: 6pt;
    color: #fff;
    display: block;
    width: 100px;
    font-family: AvenirNext;
    -ms-flex-line-pack: center;
        align-content: center;
    text-align: center;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
}

.contact:hover {
    cursor: pointer;
}

.contact .line {
    height: 40px;
    width: 1px;
    margin-left: 50px;
    margin-top: 5px;
    background: #fff;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
}

.contact:hover .line {
    height: 50px;
    margin-top: 7px;
}

@media only screen and (min-device-width: 375px) and (max-device-height: 667px) {
    .project {
        height: calc(80vw);
    }
    .projects-list {
        width: 100vw;
        margin-top: 10vh;
    }
    .header {
        -ms-flex-line-pack: center;
            align-content: center;
        text-align: center;
        width: 100%;
    }
    .header .title {
        position: relative;
        margin: auto;
        left: auto;
        right: auto;
        width: 100%;
        text-align: center;
    }
    .header .title-name {
        font-family: "Raleway", sans-serif;
        font-size: 28pt;
        width: 100vw;
        min-width: 200px;
        margin-top: 10px;
        margin-bottom: 15px;
        color: #fff;
    }
    .header .subtitle {
        font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
        font-weight: 100;
        font-size: 10pt;
        width: 100vw;
        margin-top: 5px;
        margin-left: 0px;
        margin-bottom: 5px;
        color: #fff;
        position: relative;
    }
    .header .photo {
        width: 0px;
        height: 0px;
        background: #fff;
        position: relative;
        left: auto;
        top: auto;
        visibility: hidden;
        border-radius: 50%;
    }
    .menus {
        position: relative;
        right: auto;
        margin-top: 40px;
    }
}


