body {
    background: -webkit-gradient(linear, left top, left bottom, from(#0A2D40), to(#000C12));
    background: -webkit-linear-gradient(top, #0A2D40 0%, #000C12 100%);
    background: -o-linear-gradient(top, #0A2D40 0%, #000C12 100%);
    background: linear-gradient(-180deg, #0A2D40 0%, #000C12 100%);
    overflow: hidden;
    height: 100vh;
}


