.canvas {
    background: transparent;
    position: relative;
    top: 0px;
    left: 0px;
    position: absolute;
}

.canvas2d {
    top: 0px;
    left: 0px;
    position: absolute;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 12, 18, 0)), to(rgba(0, 12, 18, 1)));
    background: -webkit-linear-gradient(top, rgba(0, 12, 18, 0) 0%, rgba(0, 12, 18, 1) 100%);
    background: -o-linear-gradient(top, rgba(0, 12, 18, 0) 0%, rgba(0, 12, 18, 1) 100%);
    background: linear-gradient(-180deg, rgba(0, 12, 18, 0) 0%, rgba(0, 12, 18, 1) 100%);
}

.project-card {
    background: #FFF;
    font-size: 10pt;
    -webkit-box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.5);
            box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.5);
    position: relative;
    text-align: left;
    padding-bottom: 16pt;
}

.project-card-content {
    padding: 6px;
}

.project-card-type {
    color: rgb(202, 156, 122);
    font-size: 4pt;
    font-family: Avenir;
}

.project-card-divider {
    background: #888;
    width: 60px;
    height: 1px;
    opacity: 0.25;
}

.project-card-title {
    font-size: 8pt;
}

.project-card-text {
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 10px;
    font-size: 5pt;
    max-width: 140px;
    font-family: AvenirNext;
    line-height: 10pt;
    color: #222;
}

.project-card-year {
    position: absolute;
    bottom: 8px;
    font-size: 6pt;
    font-weight: 100;
    font-family: Helvetica;
}

.project-image {}


